*, :before, :after {
  box-sizing: border-box;
  font-family: sans-serif;
}

:root {
  --blue-background: #1aa1e6;
  --red-background: #e64d4d;
  --green-background: #0f8a4d;
  --border-color: #dadce0;
  --border-size: 1px;
  --day-padding: .25rem;
}

body {
  height: 95vh;
  width: 90vw;
  max-height: 1000px;
  max-width: 1500px;
  margin: 0 auto;
}

.calendar {
  height: 100%;
  color: #333;
  flex-direction: column;
  display: flex;
}

.header {
  width: 100%;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.header > * {
  margin-right: .5rem;
}

.header > :last-child {
  margin-right: 0;
}

.btn {
  border: 1px solid var(--border-color);
  cursor: pointer;
  color: #333;
  background: none;
  border-radius: .25rem;
  padding: .5rem 1rem;
  font-size: 1rem;
  transition: background-color .25s;
}

.btn:hover {
  background-color: #f1f3f4;
}

.btn.btn-delete {
  color: #2d0606;
  background-color: #fce9e9;
  border-color: #e64d4d;
}

.btn.btn-delete:hover {
  background-color: #f9d2d2;
}

.btn.btn-success {
  color: #052e1a;
  background-color: #e8fcf2;
  border-color: #0f8a4d;
}

.btn.btn-success:hover {
  background-color: #d1fae6;
}

.month-change-btn {
  cursor: pointer;
  width: 2rem;
  height: 2rem;
  text-align: center;
  vertical-align: middle;
  color: #333;
  background: none;
  border: none;
  border-radius: 100%;
  padding: 0;
  font-size: 1.25rem;
  transition: background-color .25s;
}

.month-change-btn:hover {
  background-color: #f1f3f4;
}

.month-change-btn:first-child {
  margin-right: -.5rem;
}

.month-title {
  font-size: 1.5rem;
  font-weight: bold;
}

.days {
  background-color: var(--border-color);
  gap: var(--border-size);
  padding: var(--border-size);
  flex-grow: 1;
  grid-template-columns: repeat(7, minmax(0, 1fr));
  grid-auto-rows: minmax(100px, 1fr);
  display: grid;
  overflow-y: auto;
}

.day {
  padding: var(--day-padding);
  background-color: #fff;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.non-month-day {
  opacity: .75;
}

.day-header {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.week-name {
  text-transform: uppercase;
  color: #777;
  font-size: .75rem;
  font-weight: bold;
}

.day-number {
  width: 1.5rem;
  height: 1.5rem;
  justify-content: center;
  align-items: center;
  font-size: .9rem;
  display: flex;
}

.day-number.active {
  background-color: var(--blue-background);
  color: #fff;
  border-radius: 50%;
}

.day:hover .add-event-btn, .add-event-btn:focus {
  opacity: 1;
}

.add-event-btn {
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
  color: #333;
  background: none;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.25rem;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.add-event-btn:hover {
  background-color: #f1f3f4;
}

.event {
  white-space: nowrap;
  cursor: pointer;
  width: 100%;
  background: none;
  border: none;
  align-items: center;
  margin-bottom: .5rem;
  padding: 0;
  font-size: 1rem;
  display: flex;
  overflow: hidden;
}

.event:last-child {
  margin-bottom: 0;
}

.all-day-event {
  color: #fff;
  border-radius: .25rem;
  padding: .15rem .25rem;
}

.all-day-event .event-name {
  overflow: hidden;
}

.event > * {
  margin-right: .5rem;
}

.event > :last-child {
  margin-right: 0;
}

.event-time {
  color: #777;
}

.color-dot {
  width: .5rem;
  height: .5rem;
  border-radius: 50%;
  flex-shrink: 0;
}

.color-dot.blue, .all-day-event.blue {
  background-color: var(--blue-background);
}

.color-dot.red, .all-day-event.red {
  background-color: var(--red-background);
}

.color-dot.green, .all-day-event.green {
  background-color: var(--green-background);
}

.events-view-more-btn {
  color: #555;
  cursor: pointer;
  background: none;
  border: none;
  font-weight: bold;
}

.modal {
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal.show {
  pointer-events: all;
}

.modal .overlay {
  width: 100%;
  height: 100%;
  background-color: #0000;
  transition: background-color .25s;
  position: fixed;
}

.modal.show .overlay {
  background-color: #00000080;
}

.modal .modal-body {
  z-index: 1;
  min-width: 300px;
  max-width: 95%;
  background-color: #fff;
  border-radius: .5rem;
  padding: 1rem;
  transition: transform .25s;
  transform: scale(0);
}

.modal.show .modal-body {
  transform: scale(1);
}

.modal-title {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  display: flex;
}

.modal-title > * {
  margin-right: .25rem;
}

.modal-title > :last-child {
  margin-right: 0;
}

.modal-title > small {
  color: #555;
}

.form-group {
  flex-direction: column;
  margin-bottom: 1rem;
  display: flex;
}

.form-group.checkbox {
  flex-direction: row;
  align-items: center;
}

.form-group.checkbox input {
  cursor: pointer;
  margin-right: 0;
}

.form-group.checkbox label {
  cursor: pointer;
  padding-left: .5rem;
}

.form-group:last-child {
  margin-bottom: 0;
}

.form-group label {
  color: #777;
  font-size: .8rem;
  font-weight: bold;
}

.form-group input {
  padding: .25rem .5rem;
}

.row {
  display: flex;
}

.row > * {
  flex-grow: 1;
  flex-basis: 0;
  margin-right: .5rem;
}

.row > :last-child {
  margin-right: 0;
}

.row.left > * {
  flex-grow: 0;
}

.sr-only {
  visibility: hidden;
  height: 0;
  width: 0;
  display: block;
}

.color-radio {
  opacity: 0;
  position: absolute;
  left: -9999px;
}

.color-radio + label:before {
  content: "";
  width: 1.75rem;
  height: 1.75rem;
  cursor: pointer;
  opacity: .25;
  border-radius: .25rem;
  display: block;
}

.color-radio:checked + label:before {
  opacity: 1;
}

.color-radio:focus + label:before {
  outline: 1px solid #000;
}

.color-radio[value="blue"] + label:before {
  background-color: var(--blue-background);
}

.color-radio[value="red"] + label:before {
  background-color: var(--red-background);
}

.color-radio[value="green"] + label:before {
  background-color: var(--green-background);
}

.hide {
  display: none !important;
}

